/*
 * 填充录像文件列表
 */
videoPlayback.prototype.fillVideoFileTable = function(p, row, idx, index) {
	var name = p.colModel[idx].name;
	var ret = "";
	if(name == 'fileIndex') {
		ret = row.id + 1;
	} else if(name == 'fileTime') {
		ret = row.timeTitle;
	} else if(name == 'videoType') {
		if(row.type == 0) {
			ret = rootParent.lang.file_normal;
		}else if(row.type == 1) {
			var vehicle = rootParent.vehicleManager.getVehicle(row.vehiIdno);
			var device = vehicle.getVideoDevice();
			var ioNames;
			if(device && device.getIoInName()) {
				ioNames = device.getIoInName().split(",");
			}
			var str = "";
			
			if(row.loc  == 1){
				if((row.arm>>0)&1 > 0){
					str += rootParent.lang.alarm_type_ungency_button;
				}
				if((row.arm>>1)&1 > 0){
					if(str != ""){
						str += ";"
					}
					str += rootParent.lang.alarm_type_overspeed;
				} 
				if((row.arm>>2)&1 > 0){
					if(str != ""){
						str += ";"
					}
					str += rootParent.lang.alarm_type_low_speed;
				}
				if((row.arm>>3)&1 > 0){
					if(str != ""){
						str += ";"
					}
					str += rootParent.lang.alarm_type_shake;
				}
				if((row.arm>>4)&1 > 0){
					if(str != ""){
						str += ";"
					}
					str += rootParent.lang.alarm_type_temperator;
				}
				if((row.arm>>5)&1 > 0){
					if(str != ""){
						str += ";"
					}
					str += rootParent.lang.alarm_type_motion;
				}
				if((row.arm>>6)&1 > 0){
					if(str != ""){
						str += ";"
					}
					str += rootParent.lang.alarm_type_upsCut;
				}
				if((row.arm>>7)&1 > 0){
					if(str != ""){
						str += ";"
					}
					str += rootParent.lang.alarm_type_rollover;
				}
				if((row.arm>>8)&1 > 0){
					if(str != ""){
						str += ";"
					}
					str += rootParent.lang.alarm_type_fatigue;
				}
				if((row.arm>>9)&1 > 0){
					if(str != ""){
						str += ";"
					}
					if(ioNames && ioNames.length >= 1){
						str += ioNames[0];
					}else{
						str += rootParent.lang.alarm_type_io1;
					}
				}
				if((row.arm>>10)&1 > 0){
					if(str != ""){
						str += ";"
					}
					if(ioNames && ioNames.length >= 2){
						str += ioNames[1];
					}else{
						str += rootParent.lang.alarm_type_io2;
					}
				}
				if((row.arm>>11)&1 > 0){
					if(str != ""){
						str += ";"
					}
					if(ioNames && ioNames.length >= 3){
						str += ioNames[2];
					}else{
						str += rootParent.lang.alarm_type_io3;
					}
				}
				if((row.arm>>12)&1 > 0){
					if(str != ""){
						str += ";"
					}
					if(ioNames && ioNames.length >= 4){
						str += ioNames[3];
					}else{
						str += rootParent.lang.alarm_type_io4;
					}
				}
				if((row.arm>>13)&1 > 0){
					if(str != ""){
						str += ";"
					}
					if(ioNames && ioNames.length >= 5){
						str += ioNames[4];
					}else{
						str += rootParent.lang.alarm_type_io5;
					}
				}
				if((row.arm>>14)&1 > 0){
					if(str != ""){
						str += ";"
					}
					if(ioNames && ioNames.length >= 6){
						str += ioNames[5];
					}else{
						str += rootParent.lang.alarm_type_io6;
					}
				}
				if((row.arm>>15)&1 > 0){
					if(str != ""){
						str += ";"
					}
					if(ioNames && ioNames.length >= 7){
						str += ioNames[6];
					}else{
						str += rootParent.lang.alarm_type_io7;
					}
				}
				if((row.arm>>16)&1 > 0){
					if(str != ""){
						str += ";"
					}
					if(ioNames && ioNames.length >= 8){
						str += ioNames[7];
					}else{
						str += rootParent.lang.alarm_type_io8;
					}
				}
			}else{
				if(row.arm == 2){
					str += rootParent.lang.alarm_type_ungency_button;
				}
				if(row.arm == 11){
					if(str != ""){
						str += ";"
					}
					str += rootParent.lang.alarm_type_overspeed;
				} 
				if(row.arm==3){
					if(str != ""){
						str += ";"
					}
					str += rootParent.lang.alarm_type_shake;
				}
				if(row.arm == 9){
					if(str != ""){
						str += ";"
					}
					str += rootParent.lang.alarm_type_temperator;
				}
				if(row.arm  == 15){
					if(str != ""){
						str += ";"
					}
					str += rootParent.lang.alarm_type_motion;
				}
				if(row.arm == 49){
					if(str != ""){
						str += ";"
					}
					str += rootParent.lang.alarm_type_fatigue;
				}
				if(row.arm == 19){
					if(str != ""){
						str += ";"
					}
					if(ioNames && ioNames.length >= 1){
						str += ioNames[0];
					}else{
						str += rootParent.lang.alarm_type_io1;
					}
				}
				if(row.arm == 20){
					if(str != ""){
						str += ";"
					}
					if(ioNames && ioNames.length >= 2){
						str += ioNames[1];
					}else{
						str += rootParent.lang.alarm_type_io2;
					}
				}
				if(row.arm == 21){
					if(str != ""){
						str += ";"
					}
					if(ioNames && ioNames.length >= 3){
						str += ioNames[2];
					}else{
						str += rootParent.lang.alarm_type_io3;
					}
				}
				if(row.arm == 22){
					if(str != ""){
						str += ";"
					}
					if(ioNames && ioNames.length >= 4){
						str += ioNames[3];
					}else{
						str += rootParent.lang.alarm_type_io4;
					}
				}
				if(row.arm == 23){
					if(str != ""){
						str += ";"
					}
					if(ioNames && ioNames.length >= 5){
						str += ioNames[4];
					}else{
						str += rootParent.lang.alarm_type_io5;
					}
				}
				if(row.arm==24){
					if(str != ""){
						str += ";"
					}
					if(ioNames && ioNames.length >= 6){
						str += ioNames[5];
					}else{
						str += rootParent.lang.alarm_type_io6;
					}
				}
				if(row.arm==25){
					if(str != ""){
						str += ";"
					}
					if(ioNames && ioNames.length >= 7){
						str += ioNames[6];
					}else{
						str += rootParent.lang.alarm_type_io7;
					}
				}
				if(row.arm==26){
					if(str != ""){
						str += ";"
					}
					if(ioNames && ioNames.length >= 8){
						str += ioNames[7];
					}else{
						str += rootParent.lang.alarm_type_io8;
					}
				}
			}
			if(str != ""){
				ret = str;
			}else{
				ret = rootParent.lang.monitor_vehiStatusAlarm;
			}
		}
	} else if(name == 'vehiIdno') {
		ret = row.vehiIdno;
	} else if(name == 'vehiChn') {
		ret = row.chnName;
	} else if(name == 'location') {
		if(row.loc == 1) {
			ret = rootParent.lang.terminalDevice;
		}else if(row.loc == 2) {
			ret = rootParent.lang.server_storage;
		}else if(row.loc == 4) {
			ret = rootParent.lang.server_down;
		}
	} else if(name == 'fileSize') {
		ret = (row.len/1024/1024).toFixed(3) + 'MB';
	} else if(name == 'fileSrc') {
		ret = row.file;
	} else if(name == 'res') {
        if(this.queryParam.ftp == 1){ //图片不显示媒体类型
            ret = '';
        }else{
            ret = getAvitemType(row.res);
		}
    } else if(name == 'stream') {
		if(this.queryParam.ftp == 1){ //图片不显示码流
            ret = '';
		}else{
            ret = getStreamType(row.stream);
		}
    } else if(name == 'store') {
		ret = getMemTypeType(row.store);
    } else if(name == 'arm1'){
        ret = formatArm1(row.arm1);
    } else if(name == 'arm2'){
        ret = formatArm2(row.arm2);
    } else if(name == 'operator') {
    	if(this.queryParam.ftp == 2){ //文件类型 录像
            if(this.is1078) {
                //FTP上传
                //TODO 暂时屏蔽ftp上传
                // ret += '<a class="upload" href="javascript:downloadFTPVideoFile(\''+row['vehiIdno']+'\',\''+row['chnName']+'\',\''+row['beginDate']+'\',\''+row['endDate']+'\',\''+row['res']+'\',\''+row['stream']+'\',\''+row['store']+'\',\''+row['arm1']+'\',\''+row['arm2']+'\',\''+row['type']+'\');" title="'+rootParent.lang.ftpUpload+'"></a>';
            }else {
                if(row.loc == 1) {
                    //设备有直接下载的权限
                    if(row.isDirect) {
                        ret += '<a class="downLoad" href="javascript:downloadVideoFile('+row['id']+');" title="'+rootParent.lang.download+'"></a>';
                    }
                    ret += '<a class="segDownLoad" href="javascript:downloadVideoFileSec(this,'+row['id']+');" title="'+rootParent.lang.segmentDownload+'"></a>';
                }else {
//			ret += '<a class="edit" href="javascript:setDownloadTaskTag('+row['id']+');" title="'+rootParent.lang.downloadTaskTag+'"></a>';
                    ret += '<a class="downLoad" href="javascript:downloadVideoFile('+row['id']+');" title="'+rootParent.lang.download+'"></a>';
                }
            }
            //录像回放
            ret += '<a class="videoback" href="javascript:videoFileReplay(this,'+row['id']+');" title="'+rootParent.lang.video_playback+'"></a>';
		}else if(this.queryParam.ftp == 1){ //图片
            ret += '<a class="downLoad" href="javascript:downloadVideoFile('+row['id']+');" title="'+rootParent.lang.download+'"></a>';
		}else if(this.queryParam.ftp == 5){ //音频
            ret += '<a class="downLoad" href="javascript:downloadVideoFile('+row['id']+');" title="'+rootParent.lang.download+'"></a>';
        }
		return ret;
	}
	return this.getColumnTitle(ret);
};


/**
 * 填充FTP录像上传列表数据
 */
videoPlayback.prototype.fillVideoFileDownLoadTable = function(p, row, idx, index) {

    var name = p.colModel[idx].name;
    var ret = "";
    if(name == 'index') {
        ret = row.id + 1;
    } /*else if(name == 'gPSStatus') {
        ret = row.gPSStatus == null ?'':row.gPSStatus;
    } */else if(name == 'devIDNO') {
        ret = row.devIDNO;
    } else if(name == 'chnMask') {
        ret = 'CH'+ (Number(row.chnMask)+1);
    } else if(name == 'fileBegTime') {
        ret = dateTime2TimeString(row.fileBegTime);
    } else if(name == 'fileEndTime') {
        ret = dateTime2TimeString(row.fileEndTime);
    } else if(name == 'arm1') {
        ret = formatArm1(row.arm1);
    } else if(name == 'arm2') {
        ret = formatArm2(row.arm2);
    } else if(name == 'resourceType') {
        ret = getAvitemType(row.resourceType);
    } else if(name == 'streamType') {
        ret = getStreamType(row.streamType);
    } else if(name == 'storeType') {
        ret = getMemTypeType(row.storeType);
    } else if(name == 'taskStatus') {
        ret = getStatus(row.taskStatus);
    } else if(name == 'upLoadPath') {
        ret = row.upLoadPath == null ?'':row.upLoadPath;
    } else if(name =='sequence'){
        ret = row.sequence;
    } else if(name == 'operator') {
        if(row.upLoadPath != null && row.upLoadPath != '' && (row.taskStatus ==3 || row.taskStatus ==4)){  //0:暂停状态 1:正在下载 2:取消 3:失败 4:成功
			if(row.taskStatus ==4){
                ret += '<span class="view" onclick="javascript:getFtpUrl(\''+row['upLoadPath']+'\')" title="'+rootParent.lang.view+'"></span>';
			}
        }else{
            var cls = row.taskStatus == 0?"downLoad":"pause";
            var tit = row.taskStatus == 0?rootParent.lang.continues:rootParent.lang.track_pause;
        	if(row.taskStatus == 2){
                cls = "downLoad";
                tit = rootParent.lang.continues;
			}

            ret += '<a id="controllDownLoad'+row.id+'" class="'+cls+'" href="javascript:controllDownLoad('+row['id']+','+row['taskStatus']+',\''+row['devIDNO']+'\','+row['sequence']+');" title="'+tit+'"></a>';
			if(row.taskStatus != 2){
                ret += '<a class="delete" href="javascript:chanelDownLoad('+row['id']+','+row['taskStatus']+',\''+row['devIDNO']+'\','+row['sequence']+');" title="'+rootParent.lang.cancel+'"></a>';
			}
        }
        return ret;
    }
    return this.getColumnTitle(ret);
}


/**
 * FTP录像上传控制指令
 * @param id
 * @param taskStatus
 * @param devIDNO
 * @param sequence
 * @returns
 */
function controllDownLoad(id,taskStatus,devIDNO,sequence){

    var action = "StandardTTSAction_controllDownLoad.action";
    var data ={};
    data.devIdno = devIDNO;
    data.sequence = Number(sequence);
    if(taskStatus == 0 || taskStatus == 2){ //当前状态暂停
        //$("#controllDownLoad"+id).removeClass('pause').addClass('downLoad');
        data.tasktype = 1;
    }else if(taskStatus == 1){ //当前正在上传
        //$("#controllDownLoad"+id).removeClass('downLoad').addClass('pause');
        data.tasktype = 0;
    }

    var this_ = this;
    //TODO FTP录像上传控制指令
    $.myajax.jsonPost(action, data, false, function(json, success) {
        if (success) {
            if(json.result == 0){
                var ele = $("#controllDownLoad"+id).parent().parent().parent().find('.sequence');
                modifyRowData(ele,'.taskStatus',taskStatus == 0?rootParent.lang.uploading:rootParent.lang.track_pause); //上传状态
                var cls = taskStatus == 0?"pause":"downLoad";
                var tit = taskStatus == 0?rootParent.lang.track_pause:rootParent.lang.continues;
                var a = '<a id="controllDownLoad'+id+'" class="'+cls+'" href="javascript:controllDownLoad('+id+','+data.tasktype+',\''+devIDNO+'\','+sequence+');" title="'+tit+'"></a>';
                a +=  '<a class="delete" href="javascript:chanelDownLoad('+id+','+data.tasktype+',\''+devIDNO+'\','+sequence+');" title="'+rootParent.lang.cancel+'"></a>'
                modifyRowHtml(ele,'.operator',a); //操作
            }
        } else {
            $.dialog.tips(rootParent.lang.sendInstructionFail, 1);
        }
    });
}


/**
 * 取消FTP上传任务
 * @param devIDNO
 * @param sequence
 * @returns
 */
function chanelDownLoad(id,taskStatus,devIDNO,sequence){

    var action = "StandardTTSAction_controllDownLoad.action";
    var data ={};
    data.devIdno = devIDNO;
    data.sequence = Number(sequence);
    data.tasktype = 2;

    var this_ = this;
    //TODO 取消FTP下载任务
    $.myajax.jsonPost(action, data, false, function(json, success) {
        if (success) {
            if(json.result == 0){
                var ele = $("#controllDownLoad"+id).parent().parent().parent().find('.sequence');
                modifyRowData(ele,'.taskStatus',rootParent.lang.cancel); //下载状态
                var a = '<a id="controllDownLoad'+id+'" class="downLoad" href="javascript:controllDownLoad('+id+','+0+',\''+devIDNO+'\','+sequence+');" title="'+rootParent.lang.continues+'"></a>';
                modifyRowHtml(ele,'.operator',a); //操作
            }0
        } else {
            $.dialog.tips(rootParent.lang.sendInstructionFail, 1);
        }
    });
}




/**
 * 新建FTP上传任务
 * @param vehiIdno 车牌号
 * @param chnName 通道名称
 * @param StartTime 开始时间
 * @param EndTime 结束时间
 * @param res 音视频类型 （1078）
 * @param stream 码流类型 （1078）
 * @param store 存储器类型 （1078）
 * @param arm1 报警1 （1078）
 * @param arm2 报警2 （1078）
 * @param videoType 录像类型（非1078）
 */
function downloadFTPVideoFile(vehiIdno,chnName,StartTime,EndTime,res,stream,store,arm1,arm2,videoType){

    var data = {};
    data.vehiIdno = vehiIdno;
    data.chn = chnName;
    data.beginDate = StartTime;
    data.endDate = EndTime;
    data.res = res;
    data.stream = stream;
    data.store = store;
    data.arm1 = arm1;
    data.arm2 = arm2;

    var vehicle = rootParent.vehicleManager.getVehicle(vehiIdno);
    var is1078 = vehicle.getVideoDevice().status.is1078Device();

    data.is1078 = is1078;

    data.videoType = videoType;
    $.dialog({id:'ftp', title:rootParent.lang.uploadTask,content:'url:VideoManagement/selectVIdeoDownload.html?data='+encodeURIComponent(JSON.stringify(data)),
        width:'360px',height:'530px', min:false, max:false, lock:true});
}

