/**
 * 加载录像下载任务数据
 */
videoPlayback.prototype.getVideoDownLoad = function(){
    //先将下载任务列表清空
    this.videoFileDowloadTable.flexClear();

    //车牌号
    var vehiIdno = $.trim($('#hidden-vehiIdnos').val());
    var data = {};
    var vehicle = _getRootFrameElement().vehicleManager.getVehicle(vehiIdno);
    var videoDevice = vehicle.getVideoDevice();
    data.vid = videoDevice.idno;
    data.loc = 1;
    //var time = $("#datepicker").datepicker('getDate');
    var strStm = dateFormat2TimeString(this.queryParam.stm);
    var year = strStm.substring(0, 4);
    var month = strStm.substring(5, 7);
    var day = strStm.substring(8, 10);
    var stmDaily = $.trim($('#startTime').val());
    var etmDaily = $.trim($('#endTime').val());

    var dateStm = year+"-"+month+"-"+day+" "+stmDaily; //开始时间
    var dateEtm = year+"-"+month+"-"+day+" "+etmDaily; //结束时间
    data.stm = NewDate(dateStm);
    data.etm = NewDate(dateEtm);

    var this_ = this;
    var url = "StandardVideoTrackAction_queryDownLoadReplay.action";
	setTimeout(function () {
        $.myajax.jsonPost(url, data, false, function(json, success) {
            if(success){
                if(json.result == 0){
                	//console.log(json.infos);
                    this_.videoFileDowloadTable.flexAppendRowJson(json.infos,true);
                }
            }
        });
    },500);


};


