/**
 * 文件列表行双击事件
 */
videoPlayback.prototype.doubleClickVideoFileRowProp = function(obj, event) {
	var id = $.trim($(obj).attr('data-id'));
	var fileInfo = this.videoFileList.get(Number(id));
	if(this.queryParam.ftp == 1){ //搜索图片
		$('#dm_video #framePreview').css('visibility','hidden'); //.contents().find('#cmsv6flash')
        $('#dm_photo').height($('#dm_video').height());
        $('#dm_photo').css('top',-$('#dm_video').height());
		$('#dm_photo').show();
        $('#dm_photo img').attr('src',fileInfo.DownUrl);
	}else{
        $('#dm_video #framePreview').css('visibility','visible');//.contents().find('#cmsv6flash')
        $('#dm_photo').hide();
        if(fileInfo != null) {
            //chnMask>0时按位进行判断 多个通道 ； 98 全部通道
            if(fileInfo.chnMask > 0 || fileInfo.chn == 98) {
                this.addVideoFileChn(obj, fileInfo, 'videoFileReplayChn', event);
            }else {
                //获取视频文件播放的服务器信息,成功后回放视频文件信息
                var title = fileInfo.vehiIdno+' - '+ this.getChnName(fileInfo.chn)+' - '+ fileInfo.timeTitle;
                this.doReplayVehicleServer(fileInfo, 0, 0, title);
            }
        }
	}

}

/**
 * 获取回放的参数
 */
videoPlayback.prototype.checkSearchParam = function() {
	//车牌号
	var vehiIdno = $.trim($('#hidden-vehiIdnos').val());
	var selectVehiNullErr = rootParent.lang.selectPoliceFirst;
	if(vehiIdno == null || vehiIdno == '') {
		$.dialog.tips(selectVehiNullErr, 2);
		return false;
	}
	//时间
	var time = $("#datepicker").datepicker('getDate');
	//文件位置 1设备，2存储服务器，4下载服务器
	var location = $.trim($("input[name='wjwz']:checked").val());
	if(location == null || location == '') {
		$.dialog.tips(rootParent.lang.ErrorFileLocation, 2);
		return false;
	}
	//如果查询的是设备，则需要设备在线
	var devIdno = null;
	var vehicle = rootParent.vehicleManager.getVehicle(vehiIdno.toString());
	
	//车辆是否被禁用
//	if(vehicle && vehicle.getIsDisable()) {
//		$.dialog.tips(rootParent.lang.unStatus, 1);
//		return false;
//	}
	//服务期限
//	if(vehicle && !vehicle.isServicePeriod()){
//		$.dialog.tips(rootParent.lang.service_period, 2);
//		return;
//	}
	//判断是否有录像回放权限
	if(!vehicle.isSupportVideoReplay()) {
		$.dialog.tips(rootParent.lang.videoForbidden, 1);
		return false;
	}
	
	//获取视频设备
	var device = vehicle.getVideoDevice();
//	if(location == 1) {
//		if(!vehicle.isOnline()) {
//			alert(rootParent.lang.video_not_online);
//			return false;
//		}
//		if(device != null && !device.isOnline()) {
//			alert(rootParent.lang.video_not_online_device);
//			return false;
//		}
//	}
	if(device == null) {
		$.dialog.tips(rootParent.lang.NullVideoDevice, 2);
		return false;
	}
	//acc关闭状态 + 808标准协议
	if (!device.isAccOpen() && device.isJT808Protocol()) {
		$.dialog.tips(rootParent.lang.acc_status_close, 1);
	}
	//设备是否被禁用
//	if(!device.getUseStatus()) {
//		$.dialog.tips(rootParent.lang.devUnStatus, 1);
//		return false;
//	}
	
	devIdno = device.getIdno();




	if(!this.is1078){
        //文件类型  2表示录像 1图片 5音频
        var fileType = $.trim($("input[name='wjlx']:checked").val());
        if(fileType == null || fileType == '') {
            $.dialog.tips(rootParent.lang.NullFileType, 2);
            return false;
        }
        //录像|图片|音频类型  0表示常规，1表示报警，-1或者2表示所有
        var videoType = $.trim($("input[name='lxlx']:checked").val());
        if(videoType == null) {
            $.dialog.tips(rootParent.lang.NullVideoType, 2);
            return false;
        }
	}

    //填充参数
    var data = {};

	data.did = devIdno;
	data.loc = location;
	
	this.queryPreData = data;
	
	var param = {};
	if(location == 1) {
		param.did = devIdno;
	}else {
		param.did = vehiIdno;
	}
	param.loc = location;
	param.stm = time;

	//ftp 文件类型
	if(this.is1078){
		if(data.loc != null && data.loc != 1){
            param.ftp = $.trim($("input[name='wjlx']:checked").val()); //文件类型
            param.vtp = $.trim($("input[name='lxlx']:checked").val()); //录像类型
            param.res = 0;
            param.stream = -1;
            param.store = 0;
		}else{
            param.ftp = 2;//录像
            param.vtp = -1;
            param.res = $('#hidden-videoType').val();
            param.stream = $('.mllx-video:checked').val();
            param.store = $('input[name="slx"]:checked').val();
		}
	}else{
        param.ftp = fileType;
        param.vtp = videoType;
	}
    param.chn = $.trim($('#vehiChn').val());
    param.label = $.trim($('#taskTag').val());



	//判断通道是否被禁用
	if(param.chn != -1 && !device.isChannelStatus(param.chn)) {
		$.dialog.tips(rootParent.lang.channelDisabled, 1);
		return false;
	}
	
	this.queryParam = param;
	this.queryVehicle = rootParent.vehicleManager.getVehicle(vehiIdno.toString());
	this.queryVehicleChannels = this.queryVehicle.getVehicleChannel();
	return true;
};

