//添加数据到时间线
videoPlayback.prototype.addTimeTableVideoChn = function() {
	if(this.addTimeTableData.length > 0) {
		//添加到录像时间线
		if(this.loadTimeLine) {
			var videoPlay_ = this;
			this.videoTimeTable.addVideoChn(this.addTimeTableData, this.queryVehicleChannels, this.queryParam.chn, function(id, relChn, chn, begTime) {
				videoPlay_.doClickVideoChnTd(id, relChn, chn, begTime);
			});
		}
	}
}

//获取视频文件信息
videoPlayback.prototype.getVideoFileInfo = function() {
	//是否停止搜索，如果停止，则不搜索
	if(!this.isSearching) {
		return;
	}
	var strStm = dateFormat2TimeString(this.queryParam.stm);
	var year = strStm.substring(0, 4);
	var month = strStm.substring(5, 7);
	var day = strStm.substring(8, 10);
	var stmDaily = $.trim($('#startTime').val());
	var etmDaily = $.trim($('#endTime').val());

	var stm = shortHour2Second(stmDaily);
	var etm = shortHour2Second(etmDaily);
	//开始时间不得大于结束时间
	if(stm >= etm) {
		$.dialog.tips(rootParent.lang.vehicle_alarmaction_timeunvalid, 1);
		this.disableForm(false);
		return;
	}

    var data = {};

    var arm = $('#hidden-armType1').val();
    var armType1 = []; //报警类型1
    var armType2 = []; //报警类型2
    if(arm != 'undefined' && arm != '' && arm != null){
        var armType = arm.split(",");
        for(var j = 0,len = armType.length; j < len; j++){
            var type1 = armType[j].split("_");
            if(type1[0] == 1){
                armType1.push(parseInt(type1[1]));
            }else{
                armType2.push(parseInt(type1[1]));
            }
        }
    }

    armType1 = replaceAry(armType1);
    armType2 = replaceAry(armType2);
    var videoPlay_ = this;



		var jsession = GetCookie('JSESSIONID');
		//$.myajax.showLoading(true, rootParent.lang.load, this);
		
		var action = 'StandardTTSAction_getVideoFileInfo.action';
   		/*action += '?DownType=2&DevIDNO='+ encodeURI(encodeURI(this.queryParam.did)) +'&LOC='+  this.queryParam.loc +'&CHN='+ this.queryParam.chn;
            action += '&YEAR='+ year +'&MON='+  month +'&DAY='+ day;
            action += '&RECTYPE='+ this.queryParam.vtp +'&FILEATTR='+ this.queryParam.ftp;
            action += '&BEG='+ stm +'&END='+ etm + '&jsession=' + jsession;

		if(this.queryParam.loc != 1 && this.queryParam.label != '') {
            action += "&LABEL=" + this.queryParam.label;
		}*/
		data.did = this.queryParam.did;
		if(this.is1078){
            data.downType = 8;
		}else{
            data.downType = 2;
		}
		data.loc = this.queryParam.loc;
		data.chn = this.queryParam.chn;
		data.year = year;
		data.mon = month;
		data.day = day;
		if(this.is1078){
			if(data.loc != null && data.loc == 1){  //搜索文件位置为终端设备
                data.fileattr = 2;//录像  文件类型
                data.rectype = -1;//录像类型  所有
			} else{
                data.fileattr = this.queryParam.ftp || 2;
                data.rectype = this.queryParam.vtp || -1;//录像类型  所有
			}
		}else{
            data.fileattr = this.queryParam.ftp;
            data.rectype = this.queryParam.vtp;
		}
		data.beg = stm;
		data.end = etm;
		data.jsession = jsession;
		if(data.fileattr == 2){ //文件类型为录像
            data.arm1 = eval(armType1.join("+")) || 0;
            data.arm2 = eval(armType2.join("+")) || 0;
            data.res = this.queryParam.res || 0;
            data.stream = this.queryParam.stream || -1;
            data.store = this.queryParam.store || 0;
		}else{
            data.arm1 = 0;
            data.arm2 = 0;
            data.res = 0;
            data.stream = -1;
            data.store = 0;
		}

		if(this.queryParam.loc != 1 && this.queryParam.label != '') {
			data.label = this.queryParam.label;
		}


		/*var action = 'http://'+ lgServer.ip + ':' + lgServer.port +'/3/5/callback=getData';
		action += '?DownType=2&DevIDNO='+ this.queryParam.did +'&LOC='+  this.queryParam.loc +'&CHN='+ this.queryParam.chn;
		action += '&YEAR='+ year +'&MON='+  month +'&DAY='+ day;
		action += '&RECTYPE='+ this.queryParam.vtp +'&FILEATTR='+ this.queryParam.ftp;
		action += '&BEG='+ stm +'&END='+ etm;
		if(this.queryParam.loc != 1 && this.queryParam.label != '') {
			action += '&LABEL='+ this.queryParam.label;
		}*/
		
		
	var videoPlay_ = this;
	this.ajaxVehicleServerObj = $.ajax({  
		type : "post",
        url : action,
        timeout: 120000,
        data : {json : encodeURIComponent(JSON.stringify(data))},
        dataType: "json",
        success : function(json){
        	//添加视频文件信息
        	videoPlay_.addVideoFileInfo(json);
        },  
        error:function(XHR, textStatus, errorThrown){
        	if(errorThrown == 'timeout') {
        		//获取视频文件信息失败！
        		$.dialog.tips(rootParent.lang.errorGetVideoFile, 2);
        	}
        	videoPlay_.disableForm(false);
         }
	});

	
}


/**
 * 将数组转换为2的倍数
 * @param armType1
 * @returns
 */
function replaceAry(armType){
    for(var i =1,len =armType.length; i<len;i++){
        armType[i+1] =armType[i]*2;
        if(i ==11){
            armType[i+1] =262144;
        }
    }
    if(armType.length>1){
        armType.splice(armType.length-1,1);
    }
    return armType;
}



//搜索视频
videoPlayback.prototype.doQueryVideo = function() {
	//正在搜索，则停止搜索
	if(this.isSearching) {
		if(this.ajaxVehicleServerObj != null) {
			this.ajaxVehicleServerObj.abort();
		}
		this.disableForm(false);
		return;
	}
	this.disableForm(true);
	//判断条件
	if(!this.checkSearchParam()) {
		this.disableForm(false);
		return;
	}
	/*if((typeof ttxVideo) != 'undefined') {
		//清除正在播放的视频
		ttxVideo.clearVideoReplay();
		//清除播放窗口标题
		ttxVideo.setTvplayTitle(rootParent.lang.video_play);
	}
	//清除地图轨迹
	this.trackPlayer.stopPlay();*/
	
	//搜索之前先将视频时间列表清空
	this.videoTimeTable.clearVideoChn();
    //搜索之前先将视频文件列表清空
    this.videoFileTable.flexClear();
    this.videoFileList.clear();

    //获取录像文件
    //this.getVideoFileInfo();

    //获取录像上传任务
    this.getVideoDownLoad();

    //判断是否1078设备更新下载table
	this.updateFtpDownLoadTable();

	//获取服务器信息，成功后获取视频文件信息
	this.doGetVehicleServer();
};

/**
 * 判断是否1078设备更新下载table
 */
videoPlayback.prototype.updateFtpDownLoadTable = function(){
	var vehiIdno = $('#hidden-vehiIdnos').val();
    var vehicle = rootParent.vehicleManager.getVehicle(vehiIdno.toString());
    // 判断是否1078设备
    var is1078 = vehicle.getVideoDevice().getStatus().is1078Device();
    if(is1078){
		this.is1078 = true;
    }else{
        this.is1078 = false;
	}
};



//获取下载视频文件服务器信息
videoPlayback.prototype.doDownloadVehicleServer = function(fileInfo) {
	//获取之前先取消上次的请求
	if(this.ajaxDownloadVehicleServerObj != null) {
		this.ajaxDownloadVehicleServerObj.abort();
	}
	
	var param = {};
	if(fileInfo.loc == 1) {
		param.did = fileInfo.devIdno;
	}else {
		param.did = fileInfo.vehiIdno;
	}
	param.loc = fileInfo.loc;
	param.ftp = fileInfo.svr; //
	//数据库取实时服务器信息
    /*var videoPlay_ = this;
    $.myajax.showLoading(true, rootParent.lang.findDownloadAddress);
    this.ajaxDownloadVehicleServerObj = this.doAjaxSubmit('StandardVideoTrackAction_queryDownloadServer.action', param, false, function(server) {
        videoPlay_.doDownloadVideoFileInfo(fileInfo, server);
    });*/
    this.ajaxDownloadVehicleServerObj = this.doDownloadVideoFileInfo(fileInfo);
}

videoPlayback.prototype.analyFileInfoServerIP = function(fileInfo) {
	var lstSvrIp = [];
	lstSvrIp.push(fileInfo.clientIp);
	lstSvrIp.push(fileInfo.lanip);
	lstSvrIp.push(fileInfo.clientIp2);
    lstSvrIp.push(fileInfo.clientIp3);
	return getComServerIp(lstSvrIp);
}

videoPlayback.prototype.analyFileInfoAddress = function(fileInfo, url) {
	var address = url.replace(fileInfo.clientIp, this.analyFileInfoServerIP(fileInfo));
	return address;
}

