//录像回放视频文件信息	接口未使用AFU 20180911
videoPlayback.prototype.doReplayVideoFileInfoEx = function(media, chn, begTime, title, rpServer, isTimeline) {

    var data = {};
    if(media.loc == 1) {
        data.devIdno = media.devIdno;
    }else {
        data.devIdno = media.vehiIdno;
    }
    data.fileLoc = media.loc;
    data.fileSvr = media.svr;
    data.fileChn = media.chn;
    data.fileBeg = media.beg;
    data.fileEnd = media.end;
    data.playIfrm = 0;
    data.playChn = chn;
    data.playFile = media.file;
    data.playBeg = begTime * 1000;
    data.playEnd = 0;
    data.downType = 5;
    var _httpHandler = new httpHandler();
    _httpHandler.httpMethod = 'get';
    _httpHandler.cmdCode = 1; // url代号
	_httpHandler.isGetStream = 1; // 需要得到流
    _httpHandler.server = 3; // 存储服务器
    data.httpHandler = _httpHandler;

    $.myajax.sendHttpRedirect(data,_httpHandler,function (json) {
		var _videoResult = $.base64.atob(json.info.data,true);
        //视频回放
        if((typeof ttxVideo) != 'undefined' && ttxVideo != null) {
            ttxVideo.setTvplayTitle(title);
            ttxVideo.startVideoReplay(title, _videoResult);
        }
    });

    //开始地图轨迹回放
    if((typeof ttxMap) != 'undefined' && ttxMap != null) {
        this.startPlayTrack(media, begTime, isTimeline);
    }
}

