<template>
  <el-select ref="mySelect" :value="setVal" :clearable="clearable" filterable :filter-method="selectFilter"
    @clear="clearHandle" @visible-change="clearForm">
    <el-option :value="setVal" class="options">
      <el-tree  
        class="tree-option"
        ref="selectTree"
        :accordion="accordion"
        :data="options"
        :props="props"
        :node-key="props.id"
        :default-expanded-keys="defaultExpandedKey"
        :filter-node-method="filterNode"
        @node-click="handleNodeClick">
        <!-- <span class="custom-tree-node" slot-scope="{node,data}" :title="data.title?data.title:node.label">{{ node.label }}</span> -->
      </el-tree>
    </el-option>
  </el-select>
</template>

<script>
export default {
  name: "el-tree-select",
  props:{
    // 配置项
    props:{
      type: Object,
      default:()=>({
        id:'id',// node-key// 传到后台的值(title的值)
        label: 'label',         // 显示名称
        children: 'children'    // 子级字段名
      })
    },    
    // 选项列表数据(树形结构的对象数组)
    options:{ type: Array, default:()=>[] },    
    // 初始值
    setVal:{ type: String, default: null },    
    // 可清空选项
    clearable:{ type:Boolean, default: true },    
    // 是否每次只打开一个同级树节点展开
    accordion:{ type:Boolean, default: true }
  },
  data() {
    return {
      inputTitle:'',// 搜索框显示的title
      parentKey:'',// 选中子节点对应的父节点
      defaultExpandedKey:[],// 默认展开的项
      curScrollTop:0,// 滚动高度
      searchVal:''// 查找内容
    }
  },
  methods: {
    // 初始化值
    initHandle(){
      this.$nextTick(()=>{
        if(this.options.length>0 && this.setVal){
          this.defaultExpandedKey = [this.parentKey];// 设置默认展开
          this.$refs.selectTree.setCurrentKey(this.setVal);// 设置默认选中
        };
        this.initScroll();
      });
    },
    // 初始化滚动条
    initScroll(){
      this.$nextTick(()=>{
        let scrollBar = document.querySelectorAll('.el-scrollbar .el-scrollbar__bar');
        scrollBar.forEach(ele => ele.style.width = 0);
        // 计算滚动条高度
        let isCurrent = 0,
        // cursDom = document.querySelector('.is-current'),
        myTree = this.$refs.selectTree.$el,
        parentsOptions = myTree.parentNode,// 父元素 .options
        cursDom = myTree.querySelector('.el-tree-node__children .is-current');// 子项
        if(cursDom){
          isCurrent = cursDom.offsetTop+cursDom.offsetHeight-200;
        };
        this.curScrollTop = isCurrent>0?isCurrent:0;
        parentsOptions.scrollTop = this.curScrollTop;// 设置滚动条高度
      })
    },
    // 切换选项
    handleNodeClick(node,n,el){
      if(!node.children){
        this.parentKey = n.parent.key;
        this.$emit('update:setVal',node[this.props.label]);
        this.inputTitle = node.title?node.title:node.label;// 搜索框显示的title
        this.$refs.mySelect.blur();
      };
    },
    // 清除选中
    clearHandle(){
      this.searchVal = '';
      this.defaultExpandedKey = [];
      this.curScrollTop = 0;
      this.clearSelected();
      this.$emit('update:setVal','');
    },
    // 清空选中样式
    clearSelected(){
      let allNode = document.querySelectorAll('.tree-option .el-tree-node')
      allNode.forEach((element)=>element.classList.remove('is-current'))
    },
    // 搜索
    selectFilter(e){
      this.searchVal = e;
    },
    filterNode(value, data, node) {
      value = value.trim();
      if (!value || data[this.props.label].indexOf(value) !== -1) return true;
      // 判断是不是选中节点的子节点
      return this.checkBelongToChooseNode(value, data, node);
    },
    // 判断传入的节点是不是选中节点的子节点
    checkBelongToChooseNode(value, data, node) {
      const level = node.level;
      // 如果传入的节点本身就是一级节点就不用校验了
      if (level === 1) return false;
      // 先取当前节点的父节点
      let parentData = node.parent;
      // 遍历当前节点的父节点
      let index = 0;
      while (index < level - 1) {
        // 如果匹配到直接返回
        if (parentData.data[this.props.label].indexOf(value) !== -1) {
          return true;
        }
        // 否则的话再往上一层做匹配
        parentData = parentData.parent;
        index ++;
      }
      // 没匹配到返回false
      return false;
    },
    clearForm(){// 下拉框出现/隐藏时触发
      this.searchVal = '';
      this.defaultExpandedKey = [];
      this.initHandle();
    }
  },
  watch: {
    options(val){// 监测传入的数组是否有值
      if(val.length>0){
        this.initHandle();
      };
    },
    searchVal(val) {
      this.$refs.selectTree.filter(val);
    }
  },
}
</script>

<style scoped>
  .el-scrollbar .el-scrollbar__view .el-select-dropdown__item{
    height: auto;
    max-height: 200px;
    padding: 0;
    overflow: hidden;
    overflow-y: auto;
  }
  .el-select-dropdown__item.selected{
    font-weight: normal;
  }
  ul li >>>.el-tree .el-tree-node__content{
    height:auto;
    padding: 0 20px;
  }
  .el-tree-node__label{
    font-weight: normal;
  }
  .el-tree >>>.el-tree-node__children .is-current .is-leaf + span{
    color: #409EFF;
    font-weight: 700;
  }
  .el-tree >>>.is-current .el-tree-node__children .el-tree-node__label,.el-tree >>>.custom-tree-node{
    color:#606266;
    font-weight: normal;
  }
</style>