                  placement="right"
                  width="364"
                  @show="findCar2gun"
                  :value="gunPoper"
                  popper-class="myPopover"
                  trigger="click">
                  <el-cascader-panel v-if="bindGunData.length>0" :options="bindGunData" @change="changeGunChoose" v-model="limitmdFormGun" :props="{expandTrigger:'hover',emitPath:false,multiple: true}" class="bindPanl findGunCas"></el-cascader-panel>
                  <div style="height: 190px;text-align: center;line-height: 190px;" v-else>暂无数据...</div>
                  <i class="el-icon-s-operation curpoint" slot="reference"></i>
                </el-popover> -->
                <!-- 改为时间段 -->
                <el-popover v-if="false" placement="right" width="280" @show="tRFindCar2Gun" :value="gunPoper" popper-class="myPopover" trigger="click">
