                  placement="right"
                  width="364"
                  @show="findGun2car"
                  :value="carPoper"
                  popper-class="myPopover"
                  trigger="click">
                  <el-cascader-panel v-if="bindCarData.length>0" :options="bindCarData" @change="changeCarChoose" v-model="bindCarNum" :props="{expandTrigger:'hover',emitPath:false}" class="bindPanl"></el-cascader-panel>
                  <div style="height: 190px;text-align: center;line-height: 190px;" v-else>暂无数据...</div>
                  <i class="el-icon-s-operation curpoint" slot="reference"></i>
                </el-popover> -->
                <!-- 改为时间段 -->
                <el-popover v-if="false" placement="right" width="280" @show="tRFindGun2Car" :value="carPoper" popper-class="myPopover" trigger="click">
                  <el-card class="box-card" v-if="tRGunToCar.length>0">
                    <div v-for="(o,i) in tRGunToCar" :key="i" class="boxcarDiv" @click="changeCarSearch(o)">
                      <span class="tit">{{imei2name[o.serialNo]}}</span>
                      <span class="timeR">[ {{o.startHour}}:00 - {{o.endHour}}:59 ]</span>
                    </div>
                  </el-card>
