                <el-table-column label="序号" prop="id" sortable="custom" align="center" width="80" type="index"></el-table-column>
                <el-table-column label="时间" width="250px" align="center">
                  <template slot-scope="{row}">
                    <span>{{countVideoTime(row)}}</span>
                  </template>
                </el-table-column>
