                    <span>{{ scope.row.devIdno}}</span>
                  </template>
                </el-table-column>
                <el-table-column label="通道" width="110px" align="center" :filters='chnArr' :filter-method="filterTag">
                  <template slot-scope="scope">
                    <span>{{ countVideoChn(scope.row)}}</span>
                  </template>
                </el-table-column>
                <el-table-column label="文件位置" width="260px" align="center">
                  <template slot-scope="scope">
                    <span>{{countVideoLoc(scope.row)}}</span>
                  </template>
                </el-table-column>
                <el-table-column label="大小(MB)" width="130px" align="center">
                  <template slot-scope="scope">
                    <span>{{countVideoFileSize(scope.row)}}</span>
                  </template>
                </el-table-column>
                <el-table-column label="文件名称" width="220px" align="center">
                  <template slot-scope="scope">
                    <span>{{scope.row.file}}</span>
                  </template>
                </el-table-column>
