    //获取当天所有的任务
    getTaskList() {
      this.carTodayNetworks={};
      qureySomeDayAllTask({
      date: new Date(this.mdForm.date.replace(/-/g, '/')).getTime(), status: [1,3]
      })
        .then(res => {
          if (res.status == 0) {
            res.data&&res.data.forEach((item,index)=>{
            if(!this.carTodayNetworks.hasOwnProperty(item.carLicenseNo)){
            this.carTodayNetworks[item.carLicenseNo]=[item.destinationId];
            }else{
            this.carTodayNetworks[item.carLicenseNo].push(item.destinationId)
            }
            })
            this.addNetworkMarker()
          
          } else {
            _t.$message.error(res.msg);
          }
        })
        .finally(() => {
         
        });
    },
     //获取所有网点
    getNetworkList() {
      let _t = this;
      lstPt({
        type:-1,
        page:1,
        limit:1000
      })
        .then(res => {
          if (res.status == 0) {
          this.networkObj={};
          res.data.records&&res.data.records.forEach((item,index)=>{
          this.networkObj[item.id]={geom:item.geom,id:item.id,name:item.name}
          })

          } else {
            _t.$message.error(res.msg);
          }
        })
        .finally(() => {
         
        });
    },
    addNetworkMarker() {
      if (this.mapvLayer) {
        this.mapvLayer.destroy();
      }
      if (this.mapvLayer1) {
        this.mapvLayer1.destroy();
      }
      let geom = [], geomDataNetwork = [], geomDataNetwork1 = [];
      //添加网点海量点
        let networks=[];
        if(this.carTodayNetworks.hasOwnProperty(this.mdForm.imeiCar)){
        networks=this.carTodayNetworks[this.mdForm.imeiCar];
        }
       
        for (let j = 0; j < networks.length; j++) {
          if(this.networkObj.hasOwnProperty(networks[j])){
            geom =this.networkObj[networks[j]].geom.slice(6, -1).split(" ")
            geomDataNetwork.push(
              {
                geometry: {
                  type: 'Point',
                  coordinates: [geom[0], geom[1]]
                },
                text: this.networkObj[networks[j]].name
              })
            geomDataNetwork1.push(
              {
                geometry: {
                  type: 'Point',
                  coordinates: [geom[0], geom[1]]
                }

              })
          }
          
        }
      let dataSet = new mapv.DataSet(geomDataNetwork);
      let dataSet1 = new mapv.DataSet(geomDataNetwork1);
      let options = {
        draw: 'text',
        avoid: true,
        size: 12,
        font: '18px Arial',
        fillStyle: '#31478F',
        shadowColor: '#31478F',
        shadowBlur: 10
      }
      this.mapvLayer = new mapv.baiduMapLayer(this.map, dataSet, options);
      let options1 = {
        fillStyle: 'rgba(255, 50, 50, 1)',
        shadowColor: 'rgba(255, 50, 50, 1)',
        shadowBlur: 30,
        globalCompositeOperation: 'lighter',
        size: 5,
        draw: 'simple'
      }
      this.mapvLayer1 = new mapv.baiduMapLayer(this.map, dataSet1, options1);

