    getRouteAndNetwork(){
        this.RNoptions=[];let belongRouteNetwork=[];
        let p1=new Promise((resolve,reject)=>{
        mapSelect().then(res => {
          if (res.data == null) {
            res.data=[]
          }
          resolve(res.data);
        }).catch(()=>{
          reject();
        });
        })
        let p2=new Promise((resolve,reject)=>{
        allDestination({ type: -1 }).then(res => {
          if (res.data.records === null) {
            res.data=[];
          }
          this.networkOptions=res.data;
          resolve(res.data);
        }).catch(()=>{
          reject();
        });
        });
        Promise.all([p1,p2]).then((data)=>{
        data[0].forEach((item,index)=>{
          let obj={value:item.id,label:item.name,children:[]};
          data[1].forEach((item1,index1)=>{
          if(JSON.parse(item.crosslobby).indexOf(item1.id)!==-1 ){
          obj.children.push({value:item.id+'-'+item1.id,label:item1.name});
          //获取到已分配路线的网点
          if(belongRouteNetwork.indexOf(item1.id)==-1){
          belongRouteNetwork.push(item1.id)
          }
          }
          }) 
          this.RNoptions.push(obj);
        })
        //未分配路线的网点
        let notBelongRouteNetwork=[];
        data[1].forEach((item,index)=>{
        if(belongRouteNetwork.indexOf(item.id)==-1){
        notBelongRouteNetwork.push({value:0+'-'+item.id,label:item.name})
        }
        })
        //把未分配路线的网点加入到一个组
        if(notBelongRouteNetwork.length){
        this.RNoptions.push({value:0,label:'未分配路线网点',children:notBelongRouteNetwork})
        }

         
        })
