    exportExcel() {

      let _t = this,
        _s = new Date(_t.mdForm.date.replace(/-/g, '/') + ' ' + _t.mdForm.timeS).getTime(),// 转成/是为了兼容MacOS
        _e = new Date(_t.mdForm.date.replace(/-/g, '/') + ' ' + _t.mdForm.timeE).getTime();
      _t.$refs['mdForm'].validate((valid) => {
        if (valid) {
              this.exportLoading=true;

          if (_s > _e) {
            _t.$message.warning("开始时间不得大于结束时间");
          } else {
            _t.$message.warning("正在导出，稍等哦");
            // 导出数据
            exportExcelReq({ excelType: '1003', title: '车辆轨迹记录', carNo: _t.mdForm.imeiCar, startTime: _s, endTime: _e }).then(res => {
              try {
                let enc = new TextDecoder('utf-8')
                let data = JSON.parse(enc.decode(new Uint8Array(res)))
                this.$message({
                  message: data.msg,
                  type: 'warning'
                });

              } catch (error) {
                let disposition = res.headers;
                let fileName = "车辆轨迹记录" + ".xls";
                let blob = new Blob([res], { type: "application/excel" }); // 假设文件为pdf
                let link = document.createElement("a");
                link.href = window.URL.createObjectURL(blob);
                link.download = fileName;
                link.click();
                link.remove();
              }
            }).finally(()=>{
              this.exportLoading=false;
            });
          }
        }
      })


