    //处理文件信息，跨天的情况
    processFileDay(data, i) {
      //	data.year = 15;
      //	data.beg = 43200;
      //	data.end = 86000;
      //文件跨天的情况 去掉前一天的时间或者后一天的时间
      //判断跨前一天，如果日期是前一天
      data.yearMonthDay = this.videoDate;
      var day = Number(data.yearMonthDay.substring(8, 10));
      //	data.day = day - 1;
      //	data.end = 86400 + 1500;
      var fileDay = Number(data.day);
      var fileRealDate = this.getFileTime(data.year, data.mon, data.day);
      if (
        !dateCompareStrDateRange(data.yearMonthDay, fileRealDate, 1) ||
        !dateCompareStrDateRange(fileRealDate, data.yearMonthDay, 1)
      ) {
        // this.loadTimeLine = false;

        data.relBeg = data.beg;
        data.relEnd = data.end;
        data.beginDate = fileRealDate + " " + this.second2ShortHourEx(data.beg);
        data.endDate = fileRealDate + " " + this.second2ShortHourEx(data.end);
        data.timeTitle = data.beginDate;
        // if (this.queryParam.ftp != 1) {//1为图片 5音频
        data.timeTitle += " - " + this.second2ShortHourEx(data.end);
        // }
      } else {
        // this.loadTimeLine = true;
        //跨前一天
        if (fileDay < day || (day == 1 && fileDay <= 31 && fileDay >= 28)) {
          data.relBeg = 0;
          data.relEnd = Number(data.end) - 86400;
          data.beginDate =
            fileRealDate + " " + this.second2ShortHourEx(data.beg);
          data.endDate =
            dateFormat2DateString(
              dateGetNextMulDay(dateStrLongTime2Date(data.beginDate), 1)
            ) +
            " " +
            this.second2ShortHourEx(data.relEnd);
          data.timeTitle = data.beginDate;
          // if (this.queryParam.ftp != 1) {
          data.timeTitle += " - " + data.endDate;
          // }
        } else if (fileDay == day && Number(data.end) > 86400) {
          //跨后一天
          data.relBeg = data.beg;
          data.relEnd = 86399;
          data.beginDate =
            fileRealDate + " " + this.second2ShortHourEx(data.beg);
          data.endDate =
            dateFormat2DateString(
              dateGetNextMulDay(dateStrLongTime2Date(data.beginDate), 1)
            ) +
            " " +
            this.second2ShortHourEx(Number(data.end) - 86400);
          data.timeTitle = data.beginDate;
          // if (this.queryParam.ftp != 1) {
          data.timeTitle += " - " + data.endDate;
          // }
        } else {
          data.relBeg = data.beg;
          data.relEnd = data.end;
          data.beginDate =
            fileRealDate + " " + this.second2ShortHourEx(data.beg);
          data.endDate = fileRealDate + " " + this.second2ShortHourEx(data.end);
          data.timeTitle = data.beginDate;
          // if (this.queryParam.ftp != 1) {
          data.timeTitle += " - " + this.second2ShortHourEx(data.end);
          // }
        }
      }
      this.videoList[i] = data;
      // console.log(this.videoList);
    },

    //获取文件时间
    getFileTime(year, mon, day) {
      var retTime = "";
      if (Number(year) == 0) {
        // var strStm = dateFormat2TimeString(this.queryParam.stm);
        var strStm = this.videoDate;
        retTime += strStm.substring(0, 4);
      } else {
        retTime += Number(year) + 2000;
      }
      retTime += "-";
      if (mon < 10) {
        retTime += "0" + mon;
      } else {
        retTime += mon;
      }
      retTime += "-";
      if (day < 10) {
        retTime += "0" + day;
      } else {
        retTime += day;
      }
      return retTime;
    },

    //时间列表通道属性添加点击事件回调函数
    doClickVideoChnTd(id, relChn, chn, begTime) {
      // let videoFileList = new Hashtable();  //视频文件列表
      // var fileInfo = videoFileList.get(Number(id));
      var fileInfo = null;
      for (let i = 0; i < this.videoList.length; i++) {
        if (this.videoList[i].id == id) {
          fileInfo = this.videoList[i];
          break;
        }
      }
      if (fileInfo != null) {
        //录像回放视频文件信息
        if (begTime <= fileInfo.relBeg) {
          begTime = fileInfo.relBeg;
        }
        if (begTime >= fileInfo.relEnd) {
          begTime = fileInfo.relEnd;
        }
        var title =
          fileInfo.devIdno +
          " - " +
          this.getChnName(relChn) +
          " - " +
          fileInfo.yearMonthDay +
          " " +
          this.second2ShortHourEx(begTime) +
          " - " +
          this.second2ShortHourEx(fileInfo.relEnd);
        //获取视频文件播放的服务器信息,成功后回放视频文件信息
        this.doReplayVehicleServer(
          fileInfo,
          chn,
          begTime - fileInfo.relBeg,
          title,
          true
        );
      }
    },
    //获取通道名
    getChnName(chn) {
      return "CH" + (Number(chn) + 1);
    },
    //获取摄像头index 比如摄像头1，则this.cameraIndex=0；
    getCameraIndex(car) {
      if (car.indexOf("摄像头") !== -1) {
        this.cameraIndex = car.charAt(car.length - 2) - 1;
      } else {
        this.cameraIndex = -1;
      }
    },
    //在时间线中添加通道 CH1,CH2,CH3,CH4.....
    addVideoChn(files, chns, chn, callback) {

      if (typeof files != "undefined" && files.length > 0) {
        if (chns != null && chns.length > 0) {
          this.addVideoChnTr(chns);
        }
        for (var i = 0; i < files.length; i++) {
          var data = files[i];
          //chnMask暂时没用，先屏蔽
          // if (data.chnMask > 0) {
          //   var maskChns = data.maskChns.split(",");
          //   for (var j = 0; j < maskChns.length; j++) {
          //     if (chn == -1 || chn == maskChns[j]) {
          //       data.relChn = maskChns[j];
          //       this.addVideoChnTd(data, callback);
          //     }
          //   }
          // } else {

          if (data.chn == 98) {
            for (var j = 0; j < chns.length; j++) {
              if (chn == -1 || chn == j) {
                data.relChn = j;
                this.addVideoChnTd(data, callback);
              }
            }
          } else {
            if (chn == -1 || chn == data.chn) {
              data.relChn = data.chn;
              this.addVideoChnTd(data, callback);
            }
          }
          // }
        }
        let timeline = new timelineInfo();
        timeline.dynamicChnTrHeight();
