    formatCas(data, imei, name) {// 格式化级联选择器数据 data是原始数据 imei是vaue值 name是label值
      let result = [], id = 0, _t = this;
      for (let i in data) {
        let childs = [];// 格式化子数组
        data[i].forEach(x => {
          childs.push({ value: x[imei], label: x[name], disabled: false });
          _t.imei2name[x[imei]] = x[name];// 将设备号与设备名组成一个数组
        });
        result.push({ value: `i${id++}`, label: i == '' ? '未分组' : i, children: childs });
      };
      return result;
    },
    //设置默认的回放设备（从其他页面点击回放到这个页面）
    setDefalut(data, imei, type) {
      let id = 0;
      for (let i in data) {
        data[i].forEach(x => {
          if (x[imei] == this.$route.query.imei) {
            this.mdForm.date = this.$route.query.year
            this.mdForm.timeS = this.$route.query.start
            this.mdForm.timeE = this.$route.query.end
            if (type == 'car') {
              // this.mdForm.imeiCar.push(`i${id}`);
