    formatmdData(imeis, data, count) {// 处理多设备的数据,所有数据需要排序。 imeis：设备号 data：原始数据 count：枪的下标 
      let _t = this, map = _t.map,
        toData = data.filter(x => x.lat != null && x.lng != null && x.lat * 1 > 1 && x.lng * 1 > 1),// 正常的数据
        obj = { data: [], mapData: [] },// 返回的最终对象
        conData = [],// 断点续传才有的数组
        solidData = [],// 实线二维数组
        dottedData = [],// 虚线二维数组
        solidIndex = 0,// 实线数组下标
        dottedIndex = 0,// 虚线数组下标
        extraArr = [];// 实线坐标临时数组
      let _s = new Date(_t.mdForm.date.replace(/-/g, '/') + ' ' + _t.mdForm.timeS).getTime(),// 转成/是为了兼容MacOS
        _e = new Date(_t.mdForm.date.replace(/-/g, '/') + ' ' + _t.mdForm.timeE).getTime(),
        timer = (_e - _s) / 1000,// 时间轴
        timeRangeObj = {};// 时间轴对象

      function parseDom(arg) {
        var objE = document.createElement("div");
        objE.innerHTML = arg;
        return objE;
      };
      if (toData.length > 0) {
        // 处理数据
        toData.forEach((x, i) => {

          // 处理数据
          let point = Gps2Bd(new BMap.Point(x.lng, x.lat)), _d = ['东北', '东', '东南', '南', '西南', '西', '西北', '北']; // 转换为百度坐标
          //  //第一页速度为0点逆地址解析
          // // if(x.speed==0 || x.speed>60){
          //    x.address = "正在查询...";
          //   _t.handleAddress(point).then((res)=>{
          //     x.address = res;
          //     // _t.$set(x,'address',res);
          //   })
          // // }
          point.lng = +(+point.lng).toFixed(6);// 百度地图精度只到6位
          point.lat = +(+point.lat).toFixed(6);
          obj.mapData.push(point);// 百度地图点数据
          x.lngandlat = point;// 经纬度
          x.index = i + 1;// 序号
          if (x.direction) {
