        <li ${data.type == 'car' ? 'class="hide"' : ''}><span>电量</span><i>${data.battery == -1 || !data.battery ? '--' : (data.battery + ' %')}</i></li>
        <li ${(data.type == 'car' || this.power != 1) ? 'class="hide"' : ''}><span>信号强度</span><i>${data.signalPower}</i></li>
        <li><span>方向 </span><i>${data.directionCont}</i></li>
        <li><span>经纬度 </span><i>${data.lngandlat.lng} , ${data.lngandlat.lat}</i></li>
        <li><span>速度 </span><i>${data.speed} Km/h</i></li>
        <li><span>上传时间 </span><i>${data.gt}</i></li>
      </ul>`;
    },
    //清除通过查找功能在地图上显示的所有覆盖物
    deleteSomeOverlays(){
    this.startMarkers.forEach((item,index)=>{
    this.map.removeOverlay(item);
    })
    this.endMarkers.forEach((item,index)=>{
    this.map.removeOverlay(item);
    })
     this.solidPolylines.forEach((item,index)=>{
    this.map.removeOverlay(item);
    })
     this.dotPolylines.forEach((item,index)=>{
    this.map.removeOverlay(item);
    })
     this.mdPointCollection.forEach((item,index)=>{
    this.map.removeOverlay(item.mdPC);
    })
     this.mdBPointCollection.forEach((item,index)=>{
    this.map.removeOverlay(item.mdPC);
    })
     this.richMarkers.forEach((item,index)=>{
    this.map.removeOverlay(item);
    })
     this.marks.forEach((item,index)=>{
    this.map.removeOverlay(item);
    })
    this.map.removeOverlay(this.mdClickPointCollection.mdPC);
    this.startMarkers=[];
    this.endMarkers=[];
    this.solidPolylines=[];
    this.dotPolylines=[];
    this.mdPointCollection=[];
    this.mdBPointCollection=[];
    this.richMarkers=[];
    this.marks=[];
    this.mdClickPointCollection = {};// 点击的海量点对象
    },
    mdInit() {// 初始化
      let _t = this;
      _t.map.reset();
      // _t.map.clearOverlays();
      //为了避免把地图上的网点也一块被清除，不用 _t.map.clearOverlays()
      _t.deleteSomeOverlays();
      _t.mdLoad = true;// 加载动画
      // _t.mdPointCollection = [];// 所有海量点的集合，包括imei data等数据
      // _t.mdBPointCollection = [];// 断点续传的集合
      _t.mdContext = [];// 所有设备详情的数组
      _t.mdContext_list = [];// 所有设备详情的数组
      _t.pointDetails = {};// 点坐标对应的详情数据
      // _t.mdClickPointCollection = {};// 点击的海量点对象
    },
    ShPoint(e) {// 多设备轨迹点显隐
      let _t = this;
      if (e) {// 显示海量点
        _t.mdPointCollection.forEach(x => {// 正常点
          x.mdPC && x.mdPC.setPoints(x.data);
        });
        _t.mdBPointCollection.forEach(x => {// 断点续传点
          x.mdPC && x.mdPC.setPoints(x.data);
        });
        _t.mdClickPointCollection.mdPC && _t.mdClickPointCollection.mdPC.setPoints(_t.mdClickPointCollection.data);// 点击的海量点
      } else {// 隐藏海量点
        _t.mdPointCollection.forEach(x => {
          x.mdPC && x.mdPC.clear();
        });
        _t.mdBPointCollection.forEach(x => {
          x.mdPC && x.mdPC.clear();
        });
        _t.mdClickPointCollection.mdPC && _t.mdClickPointCollection.mdPC.clear();
      };
    },
    removeValid() {// 选择设备后移除验证
      this.$refs.mdForm.clearValidate(['imeiCar', 'imeiGun']);
    },
    limitChecks(a, b, n = 2) {// 限制只能选择n个设备(a指显示的真实数据对象,b指用来暂时存放以作对比的数据对象)
      let _t = this, aLen = a.length, bLen = b.length;
      // 选中的数组长度大于需要的数组长度，则对比缺少哪一个，放入需要的数组中
      // 反之，则删除
      if (aLen > bLen) {
        a.forEach(x => {
          if (b.indexOf(x) < 0) {
            b.push(x);
          };
        });
        if (bLen >= n) {// 只能选中n个
          b.shift();
        };
      } else {
        for (var i = bLen - 1; i >= 0; i--) {// 反向遍历即可解决删除错误问题
          if (a.indexOf(b[i]) < 0) {
            b.splice(i, 1);
          };
        };
      };
      for (var i = aLen - 1; i >= 0; i--) {// 反向遍历即可解决删除错误问题
        if (b.indexOf(a[i]) < 0) {
          delete a[i]
        };
      };
      a = a.filter(x => x);
      // a = b;// 将值赋给多选框绑定的变量
      return a;
    },
    poperHide() {// cascader在多选时不会关闭poper，用这个来手动关闭
      this.gunPoper = false;
      this.carPoper = false;
    },
    limitOriGunChecks() {// 限制搜索面板枪的选择个数
      // let _t = this;
      // sessionStorage.imeiGunArr = JSON.stringify(_t.limitChecks(_t.mdForm.imeiGun,_t.limitmdFormGun));
      // _t.mdForm.imeiGun = JSON.parse(sessionStorage.imeiGunArr);
      this.dataGun.push(0);
      this.dataGun.pop();// 用来更新数据，避免赋值无效
      let _t = this, aLen = _t.mdForm.imeiGun.length, bLen = _t.limitmdFormGun.length;
      // 选中的数组长度大于需要的数组长度，则对比缺少哪一个，放入需要的数组中
      // 反之，则删除
      if (aLen > bLen) {
        _t.mdForm.imeiGun.forEach(x => {
          if (_t.limitmdFormGun.indexOf(x) < 0) {
            _t.limitmdFormGun.push(x);
          };
        });
        if (bLen >= 2) {// 只能选中n个
          _t.limitmdFormGun.shift();
        };
      } else {
        for (var i = bLen - 1; i >= 0; i--) {// 反向遍历即可解决删除错误问题
          if (_t.mdForm.imeiGun.indexOf(_t.limitmdFormGun[i]) < 0) {
            _t.limitmdFormGun.splice(i, 1);
          };
        };
      };
      for (var i = aLen - 1; i >= 0; i--) {// 反向遍历即可解决删除错误问题
        if (_t.limitmdFormGun.indexOf(_t.mdForm.imeiGun[i]) < 0) {
          delete _t.mdForm.imeiGun[i]
        };
      };
      sessionStorage.imeiGunArr = JSON.stringify(_t.mdForm.imeiGun.filter(x => x));
      _t.mdForm.imeiGun = JSON.parse(sessionStorage.imeiGunArr);
    },
    changeValue() {// 切换设备时清空值
      this.mdForm.imeiCar = '';
      this.mdForm.imeiGun = [];
    },
    findCar2gun() {// 通过车找绑定的枪
      let _t = this,
        _s = new Date(_t.mdForm.date.replace(/-/g, '/') + ' ' + _t.mdForm.timeS).getTime(),// 转成/是为了兼容MacOS
        _e = new Date(_t.mdForm.date.replace(/-/g, '/') + ' ' + _t.mdForm.timeE).getTime();

      _t.gunPoper = true;
      _t.bindGunData = [];// 初始化数据
      _t.bindGunNum = _t.mdForm.imeiGun;// 选中的值
      _t.copyGunNum = [];// 清空弹窗选中
      queryByCar({
        serialNo: _t.mdForm.imeiCar,
        startTime: _s,
        endTime: _e,
        tableDate: _t.mdForm.date.replace(/-/g, '_')
      }).then(res => {// 将获取的结果插入到dataGun中
        let result = {};
        if (res.success && res.data) {
          res.data.forEach(x => {
            result[x.gunTeamName] = [];
          });
          res.data.forEach(x => {
            result[x.gunTeamName].push({ iMei: x.imei, code: x.code });
          });
          // console.log('result',result)
          _t.bindGunData = _t.formatCas(result, 'iMei', 'code');// 设置枪下拉树的数据格式
        };
      });
    },

    findGun2car() {// 通过枪找绑定的车
      let _t = this,
        _s = new Date(_t.mdForm.date.replace(/-/g, '/') + ' ' + _t.mdForm.timeS).getTime(),// 转成/是为了兼容MacOS
        _e = new Date(_t.mdForm.date.replace(/-/g, '/') + ' ' + _t.mdForm.timeE).getTime();

      let result = {},// 最后的结果
        serialNoArr = [];// 已经存在的设备
      // 初始化数据
      _t.bindCarData = [];// 弹窗中的数据
      _t.bindCarNum = _t.mdForm.imeiCar;// 选中的值
      _t.carPoper = true;
      if (_t.mdForm.imeiGun.length > 0) {
        for (let i = 0; i < _t.mdForm.imeiGun.length; i++) {// 查找与枪绑定的车
          queryByGun({
            iMei: _t.mdForm.imeiGun[i],
            startTime: _s,
            endTime: _e,
            tableDate: _t.mdForm.date.replace(/-/g, '_')
          }).then(res => {
            // console.log(res)
            if (res.success && res.data) {
              res.data.forEach(y => {
                if (!Array.isArray(result[y.carTeamName])) {// 如果不是数组
                  result[y.carTeamName] = [];
                };
              });
              res.data.forEach(z => {
                if (serialNoArr.indexOf(z.serialNo) < 0) {// 没有存在该设备号
                  result[z.carTeamName].push({ serialNo: z.serialNo, licenseNo: z.licenseNo });
                  serialNoArr.push(z.serialNo);
                };
              });
            };
            if (i == _t.mdForm.imeiGun.length - 1 && Object.keys(result).length > 0) {// 请求到最后一个数据时,且result对象有值时
              console.log(result)
              _t.bindCarData = _t.formatCas(result, 'serialNo', 'licenseNo');// 设置枪下拉树的数据格式
            };
          })
        };
      };
    },
    // 改为返回时间段的接口
    tRFindCar2Gun() {// 通过车找绑定的枪 tRCarToGun
      let _t = this,
        _s = new Date(_t.mdForm.date.replace(/-/g, '/') + ' ' + _t.mdForm.timeS).getTime(),// 转成/是为了兼容MacOS
        _e = new Date(_t.mdForm.date.replace(/-/g, '/') + ' ' + _t.mdForm.timeE).getTime();
      _t.gunPoper = true;// 打开弹窗
      _t.tRCarToGun = [];// 初始化数据
      tRQueryByCar({
        serialNo: _t.mdForm.imeiCar,
        startTime: _s,
        endTime: _e,
        tableDate: _t.mdForm.date.replace(/-/g, '_')
      }).then(res => {
        if (res.success && res.data) {
          _t.tRCarToGun = res.data;
        }
      });
    },
    tRFindGun2Car() {// 通过枪找绑定的车
      let _t = this,
        _s = new Date(_t.mdForm.date.replace(/-/g, '/') + ' ' + _t.mdForm.timeS).getTime(),// 转成/是为了兼容MacOS
        _e = new Date(_t.mdForm.date.replace(/-/g, '/') + ' ' + _t.mdForm.timeE).getTime();
      _t.carPoper = true;// 打开弹窗
      _t.tRGunToCar = [];// 初始化数据
      if (_t.mdForm.imeiGun.length > 0) {
        for (let i = 0; i < _t.mdForm.imeiGun.length; i++) {
          tRQueryByGun({
            iMei: _t.mdForm.imeiGun[i],
            startTime: _s,
            endTime: _e,
            tableDate: _t.mdForm.date.replace(/-/g, '_')
          }).then(res => {
            if (res.success && res.data) {
              _t.tRGunToCar = _t.tRGunToCar.concat(res.data);
            };
          });
        };
      };
    },
    // 选择了可能绑定的枪后，修改查找的条件
    changeGunSearch(o) {// 修改弹窗中选择的枪
      // 修改选择的枪
      this.dataGun.push(0);
      this.dataGun.pop();// 用来更新数据，避免赋值无效
      // if(this.mdForm.imeiGun.length>=2){// 如果已经存在两个设备，就需要删掉第一个
      //   this.mdForm.imeiGun.shift();
      // };
      // this.mdForm.imeiGun.push(o.imei);
      // 应该只能有一个值，因为时间段被修改了
      this.mdForm.imeiGun = [o.imei];
      this.limitmdFormGun = [o.imei];
      this.myTimeRange = [o.startHour, o.endHour];// 修改时间
    },
    changeCarSearch(o) {// 修改弹窗中选择的车
      this.mdForm.imeiCar = o.serialNo;
      this.myTimeRange = [o.startHour, o.endHour];// 修改时间
    },
    changeTimeRange() {// 修改了选择的枪支，要把时间重置
      this.myTimeRange = [0, 23];// 修改时间
    },

    changeCarChoose(e) {// 修改弹窗中选择的车
      this.mdForm.imeiCar = e;
    },
    changeGunChoose(e) {// 修改弹窗中选择的枪
      // console.log(e)
      let _t = this, eLen = e.length, copyLen = _t.copyGunNum.length;
      // 选中的数组长度大于需要的数组长度，则对比缺少哪一个，放入需要的数组中
      // 反之，则删除
      if (eLen > copyLen) {
        e.forEach(x => {
          if (_t.copyGunNum.indexOf(x) < 0) {
            _t.copyGunNum.push(x);
          };
        });
        if (copyLen >= 2) {// 只能选中两个
          _t.copyGunNum.shift();
        };
      } else {
        for (var i = copyLen - 1; i >= 0; i--) {// 反向遍历即可解决删除错误问题
          if (e.indexOf(_t.copyGunNum[i]) < 0) {
            _t.copyGunNum.splice(i, 1);
          };
        };
      };
      sessionStorage.kk = JSON.stringify(_t.copyGunNum);
      _t.bindGunNum = JSON.parse(sessionStorage.kk);// 将值赋给多选框绑定的变量
      _t.limitmdFormGun = JSON.parse(sessionStorage.kk);
      _t.mdForm.imeiGun = JSON.parse(sessionStorage.kk);// 将值赋给多选框绑定的变量
    },
    lTabActiveFalse() {// 设置为视频回放
      let _t = this;
      _t.lTabActive = false;
      _t.showVideo();// 初始化播放器
    },
    winChangeFunc() {// 点击tab时，如果是最小化则转为窗口化
      if (this.winChange == 'winC-0') {
        this.winChange = 'winC-1';
      };
    },
    winChange1Func() {// 点击tab时，如果是最小化则转为窗口化
      if (this.winChange1 == 'winC-0') {
        this.winChange1 = 'winC-1';
      };
    },
    toPauseCar() {// 将车切换至暂停
      this.mdContext.forEach(x => {
        if (x.progress.startOrpause == false) {// 如果在播放就暂停
          x.mdStartAnimate();
        };
      })
    },
    toStartCar() {// 将车切换至播放
      this.mdContext.forEach(x => {
        if (x.progress.startOrpause == true) {// 如果在暂停就播放
          x.mdStartAnimate();
        };
      })
    }
  }
};
</script>

<style lang="scss">
$leftBarW: 240px;
$headerBarH: 120px;
$lLeftW: 195px;
$theme-color: #324890;

// 新样式
#newLayout {
  position: fixed;
  left: $leftBarW;
  top: $headerBarH;
  right: 0;
  bottom: 0;
  z-index: 999;
  display: flex;
  .shrink {
    // 侧边栏收缩
    position: fixed;
    left: calc(#{$leftBarW} + #{$lLeftW});
    // top: 50%;
    top: calc(#{$headerBarH});
    z-index: 9;
    cursor: pointer;
    font-size: 26px;
    color: #bbbbbb;
    transition: all 0.5s;
    &:hover {
      color: #357fff;
    }
    &.menuHide {
      left: calc(#{$lLeftW});
    }
    &.pop {
      left: $leftBarW;
      &.menuHide {
        left: 30px;
      }
    }
  }
  .l-left {
    // 侧边栏
    width: $lLeftW;
    background-color: #fff;
    display: flex;
    align-items: center;
    flex-direction: column;
    overflow-x: hidden;
    overflow-y: auto;
    transition: all 0.1s;
    &::-webkit-scrollbar {
      display: none;
    }
    // 显示隐藏
    &.sOrh {
      width: 0;
    }
    // 切换视频回放与轨迹回放的tab
    .l-tab {
      display: flex;
      flex-direction: column;
      .l-btn {
        // 两个按钮
        display: flex;
        // flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 170px;
        height: 60px;
        margin-top: 20px;
        background-color: #fafafa;
        cursor: pointer;
        border-radius: 3px;
        .l-img {
          width: 32px;
          height: 28px;
          background-size: 66px 90px;
          background-image: url("../../../static/images/h-l-left.png");
          background-repeat: no-repeat;
        }
        span {
          font-size: 14px;
          color: #999;
        }
        // 视频回放
        &.l-video {
          .l-img {
            background-position: -33px 0;
            margin-right: 20px;
          }
        }
        // 轨迹回放
        &.l-track {
          margin-top: 0;
          .l-img {
            background-position: -33px -33px;
            margin-right: 20px;
          }
        }
        // 选中时
        &.active {
          background-color: #eaf5ff;
          .l-img {
            background-position-x: 0;
          }
          span {
            color: #3c7efe;
          }
        }
        // 划过时
        &:hover {
          background-color: #eaf5ff;
        }
      }
    }
    // 轨迹回放查找面板
    .l-trackPanel {
      display: flex;
      flex-direction: column;
      align-items: center;
      // 查找面板
      .l-searchPanel {
        // 所有label样式
        .el-form-item__label {
          font-weight: normal;
          line-height: 26px;
        }
        .el-form-item__content,
        .el-cascader {
          // 下拉或者输入框父元素、cascader
          // line-height: 26px!important;
          //  height: 57px!important;
        }

        .el-cascader {
          .el-input__inner {
            max-height: 90px !important;
          }
        }

        .el-input {
          .el-input__inner {
            // 输入框
            height: 26px;
            line-height: 26px;
            width: 141px;
            background-color: #fafafa;
            border-color: $theme-color;
            padding: 0 20px;
          }
        }
        .timeHind {
          height: 39px;
          font-size: 14px;
          color: #606266;
        }

        .elSlider {
          width: 141px;
        }

        .el-cascader__search-input {
          background-color: #fafafa;
        }
        .el-form-item {
          // 输入框
          display: flex;
          align-items: center;
          margin-bottom:14px;
          .el-date-editor.el-input,
          .el-date-editor.el-input__inner {
            width: 141px;
          }
          .el-input__icon {
            line-height: 26px;
          }
        }
        .el-input__prefix {
          // 日期图标
          left: auto;
          right: 0;
          color: #3c7efe;
        }
        .l-trackPoint {
          // 切换轨迹点
          display: flex;
          justify-content: space-between;
          margin: 22px 10px;
        }
        .trackRadio{
        .el-radio-button__inner{
          padding:8px;
        }
           margin-top:20px;
        }
        .historyS,
        .exportBtn {
          // 查找按钮
          width: 100%;
          height: 30px;
          line-height: 30px;
          padding: 0;
        }
        .exportBtn {
          margin-left: 0;
          margin-top: 10px;
        }
      }
      // 测距工具
      .l-measure {
        width: 62px;
        height: 62px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        background-color: #eaf5ff;
        border-radius: 3px;
        cursor: pointer;
        .l-img {
          width: 24px;
          height: 24px;
          margin-bottom: 5px;
          background-repeat: no-repeat;
          background-image: url("../../../static/images/h-l-left.png");
          background-position: 0 -66px;
        }
        span {
          color: #3c7efe;
          font-size: 12px;
        }
        // active
        &:active {
          opacity: 0.8;
        }
      }
      // 查找结果显示面板
      .l-searchRes {
        width: 210px;
        .imeiPanel {
          // 设备详情
          padding: 0;
          margin: 0;
          li {
            display: flex;
            flex-direction: column;
            justify-content: center;
            padding: 10px;
            background-color: #fafafa;
            border-radius: 8px;
            margin-top: 10px;
            &:nth-child(2n) {
              background-color: #eaf5ff;
            }
            // 各个进度条样式
            // 对应js中的车枪样式（lineStyle，pointStyle）
            &.li-car {
              // 车
              .progress .el-slider {
                .el-slider__bar,
                .el-slider__runway {
                  // 线的颜色对应拖动条
                  background-color: #09c030;
                }
                .el-slider__button {
                  // 点的颜色对应拖动块
                  background-color: #1a853d;
                }
              }
            }
            &.li-gun {
              // 枪
              &[data-lineColor*="021BFD"] {
                .progress .el-slider {
                  .el-slider__bar,
                  .el-slider__runway {
                    // 线的颜色对应拖动条
                    background-color: #021bfd;
                  }
                  .el-slider__button {
                    // 点的颜色对应拖动块
                    background-color: #2a53f8;
                  }
                }
              }
              &[data-lineColor*="8933ec"] {
                .progress .el-slider {
                  .el-slider__bar,
                  .el-slider__runway {
                    // 线的颜色对应拖动条
                    background-color: #8933ec;
                  }
                  .el-slider__button {
                    // 点的颜色对应拖动块
                    background-color: #fb1dfb;
                  }
                }
              }
            }
            //./ 各个进度条样式
            * {
              font-style: normal;
              font-size: 13px;
            }
            & > div {
              display: flex;
              align-items: center;
            }
            .icons {
              // 图标等
              flex-wrap: wrap;
              img {
                width: 30px;
              }
              .imeiLine {
                width: 35px;
                height: 5px;
                margin: 0 5px;
              }
              .imeiPoint {
                width: 10px;
                height: 10px;
                border-radius: 50%;
              }
            }
            // 内容详情由于是v-html渲染，样式放在全局中
            .progress {
              // 进度条与播放停止按钮
              flex-direction: column;
              .el-slider {
                width: 90%;
                // 修改进度条样式
                .el-slider__bar {
                  // 已拖动部分
                  height: 4px;
                }
                .el-slider__runway {
                  // 未拖动部分
                  height: 4px;
                  margin: 10px 0;
                }
                .el-slider__button {
                  // 拖块
                  border: 0;
                  background-color: #409eff;
                  width: 12px;
                  height: 12px;
                }
              }
              i {
                // 播放停止按钮
                cursor: pointer;
                font-size: 12px;
                color: #409eff;
                border: 1px solid;
                border-radius: 50%;
                padding: 1px;
                &.l-start {
                  // 播放
                  margin-right: 20px;
                }
                &.l-stop {
                  // 停止
                  color: #f14242;
                }
              }
            }
          }
        }
      }
    }
    // 视频回放查找面板
    .l-videoPanel {
      // width: 223px;
      margin-top: 22px;
      .l-searchPanel .el-input__inner {
        // 输入框放长些
        width: 151px;
      }
      .el-radio-group {
        // 文件位置由上往下排列
        display: flex;
        flex-direction: column;
        margin-bottom: 15px;
        .el-radio {
          margin-top: 10px;
        }
      }
    }
  }
  .l-right {
    // 地图与表格
    background-color: #f6f8fc;
    flex: auto;
    padding: 20px 10px 10px;
    position: relative;
    #videoBox.l-map.winC-1 {
      // 避免视频回放会变形
      bottom: 290px;
      height: unset !important;
      width: unset !important;
    }
    .l-video-table.l-table.winC-1 {
      // 视频回放的选择进度的高度
      height: 260px;
      .el-tabs__content {
        height: 222px !important;
      }
    }
    .l-map {
      // 地图 使用定位布局，是为了避免地图最小化时，路线、视频出现问题
      position: absolute;
      left: 20px;
      top: 20px;
      right: 20px;
      bottom: 80px;
      transition: all 0.5s;
      #map,
      #video {
        width: 100% !important;
        height: 100% !important;
      }
      // 窗口化时
      &.winC-1 {
        bottom: 240px; // 200+40 200是表格窗口化时的高度 40是边距
      }
      // 弹窗样式
      h5 {
        margin: 0;
      }
      .myRichMarker {
        width: 308PX;
        height: 264PX;
        border: 0;
        padding: 0;
        padding-top: 2PX;
        background: transparent url("../../../static/images/h-l-win.png")
          no-repeat;
        * {
          color: #fff;
          font-weight: normal;
        }
        & + .myClose {
          // 隐藏关闭按钮
          top: -4PX;
          font-size: 22PX;
          left: 193PX;
          right: auto;
          color: #fff;
        }
        // 标题
        .l-tit {
          margin: 0;
          margin-bottom: 20PX;
          width: 256PX;
          text-align: center;
        }
        // 内容
        li {
          width: 266PX;
          margin-bottom: 10PX;
          span:nth-child(1) {
            display: inline-block;
            width: 60PX;
            margin: 0 15PX 0 20PX;
          }
        }
        li.addItem {
          .el-icon-circle-plus-outline {
            color: rgb(163, 10, 10) !important;
            font-size: 18PX;
            cursor: pointer;
            z-index: 1000;
          }
          .el-icon-circle-plus-outline:hover {
            color: #d80707 !important;
          }
          span {
            display: inline-block;
          }
        }
        // 枪的内容
        .myInfo.gun {
          margin-top: -10PX;
          li {
            margin-bottom: 5PX;
          }
        }
      }
    }
    .l-table {
      // 表格
      position: absolute;
      left: 20px;
      right: 20px;
      bottom: 20px;
      height: 60px;
      overflow: hidden;
      background-color: #fff;
      transition: all 0.5s;
      // 窗口化工具
      .l-window {
        position: absolute;
        right: 0;
        top: 11px;
        z-index: 9;
        i {
          color: #999;
          font-size: 14px;
        }
        .el-tooltip {
          margin-right: 20px;
          cursor: pointer;
          outline: none;
        }
      }
      // 最小化
      &.winC-0 {
        height: 40px;
      }
      // 窗口化
      &.winC-1 {
        height: 200px;
        .el-tabs__content {
          // 表格
          overflow: auto;
          height: 116px !important; // 阻止js修改
        }
      }
      // 最大化
      &.winC-2 {
        height: calc(100vh - #{$headerBarH} - 30px); //30是padding或margin
        .el-tabs__content {
          // 表格
          overflow: auto;
          height: calc(100vh - 210px) !important; // 阻止js修改
        }
      }
      // tab页
      .activeName {
        .el-tabs__header {
          margin: 0;
          .el-tabs__nav {
            margin-left: 50px;
          }
        }
        .el-tabs__item {
          // 标题栏
          height: 40px;
          line-height: 40px;
          font-size: 14px !important;
        }
      }
      // 表格样式
      th,
      td {
        border: 0;
      }
      tbody {
        tr {
          height: 30px;
        }
        td {
          padding: 6px 0;
        }
        tr:nth-child(2n-1) {
          background-color: #fafafa;
        }
      }
    }
    //底部分页器
    .fixedBar {
      position: fixed;
      bottom: 22px;
      z-index: 1;
      transform: translateX(50%);
      padding: 0 !important;
      .el-pagination {
        background-color: white !important;
        display: flex;
        justify-content: center;
        font-size: 14px !important;
        .el-pagination__editor.el-input .el-input__inner {
          height: 30px !important;
          font-weight: 500;
        }
        button,
        .el-pagination__total,
        .el-input--mini .el-input__inner,
        .el-pager li {
          height: 30px !important;
          line-height: 30px !important;
          font-size: 14px !important;
        }
        .el-pager li {
          min-width: 30px !important;
          border-radius: 2px;
          background: rgba(240, 240, 240, 1);
          &.active {
            background-color: rgba(60, 126, 254, 1);
          }
        }
      }
    }
  }
}
