import request from '../request'
import axios from 'axios'

//获取回放视频文件（由于必须用jsonp,用axios出问题，暂时不用这个）
export function fileList(data) {
    return request({
        type:'post',
        url: 'http://www.beidoulab.work:6605/3/1/callback=getData',
        method: 'post',
        params:data,
        // headers:{'content-type':'application/x-www-form-urlencoded'},
        dataType: 'jsonp',
        jsonp:'callback',

        cache:false,
        
    })
}
//获取轨迹数据
export function trackList(data) {
    return request({
        url: '/StandardApiAction_queryTrackDetail.action',
        method: 'post',
        data,
        cache: false,
        dataType: "json",
        headers:{'content-type':'multipart/form-data'}
    })
}
//测试轨迹数据上传
export function trackListTest(data) {
    return request({
        url: 'http://beidoulab.work/app/map/create',
        method: 'post',
        data,
        headers:{'content-type':'application/json'}
    })
}
